$breakpoint-tablet: 1024px;
$laptop-screen: 1600px;
$office-screen: 1920px;

// Media query
@media (max-width: $breakpoint-tablet) {
  .name-container {
    div.vhf-mode-container {
      display: inline-flex;
      justify-content: space-around;
      width: 100%;
    }
  }
}

@media (min-width: $office-screen) {
}

.p-card {
  width: 15rem;
}

div.vhf-mode-container {
  display: inline-flex;
  justify-content: space-between;
  width: 100%;
}

span.mode {
  text-transform: uppercase;
  font-weight: bolder;
  text-align: center;
  background: rgba(255, 200, 200, 0);
  border: solid 5px rgba(200, 0, 0, 0);
  color: rgba(200, 200, 200, 1);
  padding: 0px;
  border-radius: 5px;
  padding: 5px;
  margin: 0;
  line-height: 20px;
  width: 5rem;
}
span.mode.activated {
  background: #c8e6c9;
  border: solid 5px #4caf50;
  color: #4caf50;
}
span.mode > p {
  margin: 0;
  padding: 0;
}
