.p-menubar {
  border-radius: 0 !important;
}

#root > div.p-menubar.p-component.header-container > div.p-menubar-end > div > span.date {
  margin-left: 25rem;
  font-weight: bold;

}


#root > div.p-menubar.p-component.header-container > div.p-menubar-start > div > span.mode {
  margin-left: 6rem;
  font-weight: bold;
}

#root > div.p-menubar.p-component.header-container > ul > li > a > span.p-menuitem-text {
  color: #fafafa;
  font-weight: bold;
  display: inline-block;
  justify-content: space-between;
}

.alerts {
  margin-right: 100px;
}

.block {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.mode {
  margin-left: 50px;
  background: red;
}

.header-container {
  height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10000;
  box-shadow: 0 0 4px rgba(0, 0, 0, 25%);
  .logo-engie {
    display: block;
    max-height: 100%;
    fill: #0af;
    width: 5rem;
  }

  .p-menubar-end {
    p {
      margin-bottom: 0.5rem;
    }
  }

  .engie-logo {
    width: 5rem;
  }
}
