.date-picker-container {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
  .calendar-container {
    display: flex;
    width: 100%;
    justify-content: center;
    .start-date-container {
      margin-right: 1rem;
      text-align: center;
    }
    .end-date-container {
      margin-left: 1rem;
      text-align: center;
    }
  }
}

.StatisticsContainer {
  display: flex;
  justify-content: space-around;
}

.bulletin-table-container {
  margin-top: 2rem;
}

.StatisticPanel {
  vertical-align: top;
  width: 300px;
}
