.event-side-bar {
  .p-button {
    width: 100%;
  }
  h3 {
    margin-bottom: 1.2rem;
    text-align: center;
    font-weight: 700;
  }
  h5 {
    margin-bottom: 1rem;
  }
}

.select-file-container {
  .p-dropdown {
    width: 100%;
  }
  .mounth-dropdown,
  .file-dropdown {
    margin-bottom: 1rem;
  }
}

.select-time-container {
  .p-calendar {
    width: 100%;
  }
  P {
    margin-bottom: 0.6rem;
  }
  .select-start-date {
    margin-bottom: 1rem;
  }
}

.criticity-container {
  .p-multiselect {
    width: 100%;
  }
}

.page-EventLogs {
  .event-log-table {
    .p-badge {
      min-width: 5rem;
    }
    tr {
      height: 2.3rem;
    }
  }
  hr {
    padding: 10px;
  }
}

.mb {
  margin-bottom: 0.3rem;
}

.event-type-selection {
  label {
    margin-bottom: 0;
    margin-left: 0.5rem;
  }
}
