$breakpoint-tablet: 1024px;
$laptop-screen: 1600px;
$office-screen: 1920px;

// Media query
@media (max-width: $breakpoint-tablet) {
  .components-container {
    .monitoring-fieldset {
      margin-bottom: 2rem;
      .first-part {
        display: initial;
        .p-card {
          width: 100%;
          margin-bottom: 1.5rem;
          .p-card-title {
            text-align: center;
          }
        }
      }
      .second-part {
        display: initial;
        .p-card {
          width: 100%;
          margin-bottom: 1.5rem;
          .p-card-title {
            text-align: center;
          }
        }
      }
      .third-part {
        padding: 0 0.5rem;
      }
    }
  }
}

@media (min-width: $office-screen) {
}

.spinner-container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.monitoring-fieldset {
  margin-bottom: 2rem;
  .first-part {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 2rem;
    .p-card {
      width: 30rem;
    }
  }
  .second-part {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 2rem;
  }
  .third-part {
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
  }
  .fourth-part {
    padding: 0 2rem;
  }
}

.monitoring-fieldset > * {
  display: block;
}

.p-fieldset-content {
  display: block;
}
