$breakpoint-tablet: 1024px;
$laptop-screen: 1600px;
$office-screen: 1920px;

// Media query
@media (max-width: $breakpoint-tablet) {
  .CardListBox .template-container .info-container {
    display: grid;
    width: 100%;
    grid-template-columns: 8fr 1fr;
    .red-round,
    .green-round {
      margin: 0 auto;
    }
  }
}

@media (min-width: $office-screen) {
}

.monitoring-container {
  .pure-supervision-container {
    display: flex;

    justify-content: space-evenly;

    margin-bottom: 2rem;

    .p-card {
      width: 17rem;

      .p-card-title {
        height: 4rem;
      }

      .p-card-content {
        display: grid;

        grid-template-columns: 4fr 1fr;

        .red-round {
          width: 25px;

          height: 25px;

          border-radius: 25px;

          background-color: red;

          justify-content: end;
        }

        .green-round {
          width: 25px;

          height: 25px;

          border-radius: 25px;

          background-color: green;

          justify-content: end;
        }
      }
    }
  }

  .supervision-rtc-container {
    display: flex;

    justify-content: space-around;

    margin-bottom: 2rem;

    .p-card {
      width: 17rem;

      .p-card-title {
        height: 4rem;
      }

      .state-container,
      .broadcast-container {
        display: grid;

        grid-template-columns: 4fr 1fr;
      }

      .p-card-content {
        .red-round {
          width: 25px;

          height: 25px;

          border-radius: 25px;

          background-color: red;

          justify-content: end;
        }

        .green-round {
          width: 25px;

          height: 25px;

          border-radius: 25px;

          background-color: green;

          justify-content: end;
        }

        .grey-round {
          width: 25px;

          height: 25px;

          border-radius: 25px;

          background-color: grey;

          justify-content: end;
        }
      }
    }
  }

  .stap-server-container {
    .p-progressbar {
      margin-top: 0.3rem;
    }

    .data-container {
      margin-bottom: 1rem;
    }
  }

  .services-supervision-container {
    display: flex;

    justify-content: space-evenly;

    .template-container {
      display: grid;

      grid-template-columns: 4fr 1fr;

      .red-round {
        width: 25px;

        height: 25px;

        border-radius: 25px;

        background-color: red;

        justify-content: end;
      }

      .green-round {
        width: 25px;

        height: 25px;

        border-radius: 25px;

        background-color: green;

        justify-content: end;
      }
    }

    .middle {
      margin: 0 3rem;
    }

    .p-card {
      width: 17rem;

      .p-card-title {
        height: 4rem;
      }

      .p-listbox {
        border: initial;

        .p-listbox-list .p-listbox-item {
          padding: 0.5rem 0 0 0.5rem;
        }
      }
    }
  }
}
