.login-controller {
  height: 85vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 45%;
  margin: 0 auto;
  label {
    display: block;
    margin-bottom: 0.5rem;
    color: #495057;
    font-size: 14px;
  }
  .username-field,
  .password-field {
    width: 100%;
  }
  .password-field-container {
    padding: 0.5rem 0;
  }
}
