.add-account-dialog,
.update-account-dialog {
  width: 25rem;
  .p-multiselect,
  .p-inputtext,
  .p-password {
    width: 100%;
  }
  .p-dialog-content {
    div {
      margin-bottom: 0.5rem;
    }
  }
}

.monitoring-accounts-manager-container {
  .role-users {
    background: #ffcdd2;
    border: solid 2px #b71c1c;
  }
  .role-admins {
    background: #f8bbd0;
    border: solid 2px #e91e63;
  }
  .role-supervisors {
    background: #e1bee7;
    border: solid 2px #9c27b0;
  }
}
