li.SideBarItem {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  display: block;
}

.item-selected {
  background: #01afef;
}

li.SideBarItem:hover {
  background: #01afef;
}

li.SideBarItem a {
  display: block;
  color: #fafafa;
  text-decoration: none;
  padding: 5px;
  padding-right: 20px;
}

li.SideBarItem img.SideBarItemIcon {
  width: 1.7rem;
  margin: 5px 10px;
}
