.CardListBox {
  .p-progress-spinner {
    width: 100%;
  }
}

.CardListBox .Card {
  width: 30rem;
}

.CardListBox .template-container {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  .info-container {
    display: grid;
    grid-template-columns: 11fr 1fr;
    .name-container {
      margin-right: 1rem;
    }
  }
}
.CardListBox .template-container p {
  margin: 0;
  padding: 0;
}
.CardListBox .red-round {
  width: 25px;
  height: 25px;
  border-radius: 25px;
  background-color: red;
  justify-content: end;
  min-width: 25px;
  min-height: 25px;
}
.CardListBox .green-round {
  width: 25px;
  height: 25px;
  border-radius: 25px;
  background-color: green;
  justify-content: end;
}

.p-card-content {
  .no-data {
    text-align: center;
  }
}
