nav.sidebar-container {
  position: fixed;
  height: 100vh;
  min-width: 260px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  margin: 0;
  padding: 0;
  list-style: none;
  padding-top: 4.5rem;
  background: #424242;
  overflow-y: auto;
}

nav.sidebar-container > ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav.sidebar-container > div.WelcomeUser {
  background: rgba(0, 0, 0, 1);
  text-align: center;
  padding: 10px;
}

nav.sidebar-container > div.WelcomeUser img {
  width: 50px;
}
nav.sidebar-container > div.WelcomeUser p {
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
  color: #01afef;
  margin: 0px;
  padding: 0px;
}

.version-container {
  position: absolute;
  bottom: 0;
  color: white;
  padding: 0 0 0 1rem;
}

.version-container p {
  margin-bottom: 0.3rem;
}
