.page-bulletins {
  .p-badge {
    min-width: 7rem;
  }
}

.pi {
  cursor: pointer;
}

.pi-info-circle {
  color: #00aaff;
}

.table-header {
  display: flex;
  justify-content: space-between;
}

.p-datatable .p-datatable-wrapper .p-datatable-thead {
  background-color: black;
}

.p-datatable .p-datatable-wrapper .p-datatable-tbody .rowOpen {
  background-color: #e3f2fd;
}

.settings-wrapper {
  // height: 55vh;
  display: flex;
  justify-content: space-around;
  .container {
    text-align: justify;
  }
}

.setting-title {
  text-decoration: underline;
  font-weight: 600;
}

.play-error {
  opacity: 0.5;
  cursor: no-drop;
}

.customer-badge {
  width: 120px;
  display: inline-block;
  margin: auto;
  border-radius: 5px;
  cursor: default;
}

.customer-badge-mini {
  width: auto;
  padding: 0px 10px;
  display: inline-block;
  margin: auto;
  border-radius: 5px;
  cursor: default;
}

.background-grey {
  background-color: #e0e0e0 !important;
}

.customer-badge.status-outdated {
  background: #eeeeee;
  border: solid 3px #9e9e9e;
  color: #9e9e9e;
  font-weight: bold;
}

.tooltip-container .tooltip-text {
  visibility: hidden;
  width: 120px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip-container .tooltip-text::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.tooltip-container:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

p.DecodedSentence {
  margin: 0;
}
