.broadcast-message-container {
  .p-fieldset {
    margin-bottom: 2rem;
  }
  .creation-message-container {
    .record-message-container {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .player-container {
      display: flex;
      justify-content: space-evenly;
    }
    .synthesis-message-container {
      display: flex;
      justify-content: center;
      .synthesis-message-textarea {
        height: 12rem;
        width: 50%;
      }
      .button-container {
        margin-left: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
      }
      .buttons-container {
        display: flex;
        width: 100%;
        justify-content: space-evenly;
      }
    }
  }
}
