$breakpoint-tablet: 1024px;
$laptop-screen: 1600px;
$office-screen: 1920px;

// Media query
@media (max-width: $breakpoint-tablet) {
  body {
    #root {
      .dom-container {
        grid-template-columns: 25% 75%;
        .components-container {
          margin-top: 4rem;
        }
      }
    }
  }
}

@media (min-width: $office-screen) {
  body {
    #root {
      .dom-container {
        grid-template-columns: 14% 86%;
        .components-container {
          margin-top: 4rem;
        }
      }
    }
  }
}

// CSS on normal laptop
body {
  user-select: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  // overflow: hidden;
}
#root {
  .dom-container {
    height: 100vh;
    display: grid;
    grid-template-columns: 18% 82%;
    .sidebar-container {
      z-index: 8000;
    }
    .components-container {
      margin-top: 5rem;
    }
  }
}

*:disabled {
  cursor: not-allowed;
}

